import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';


interface ServiceResponse
{
  success : boolean
}

@Injectable({
  providedIn: 'root'
})


export class LoginService {

  constructor(private http: HttpClient) { }

  apiUrl: string = "http://localhost:4000/";

  authenticateUser(username: string, password: string): Observable<boolean> {
    return this.http.get<boolean>(this.apiUrl + 'authenticateUser/' + username + '/' + password).pipe(
      tap(data => console.log('All: ' + JSON.stringify(data))),
      catchError(this.handleError)
    );
  }

  saveBranchMasterDetails(BranchName : string,  Address: string, Email : string)
  {
    return this.http.post<ServiceResponse>(this.apiUrl + 'saveBranchMasterDetails/' ,
    { BranchName, Address , Email}).pipe(
      tap(data => console.log('All: ' + data.success)),
      catchError(this.handleError)
    );
  }

  private handleError(err: HttpErrorResponse) {
    let errorMessage = '';
    if(err.error instanceof ErrorEvent){
      errorMessage = `An error occured: ${err.error.message}`;
    } else{
      errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
    }
    console.error(errorMessage);
    return throwError(errorMessage);
  }


}
